<template>
  <div class="update-pwd">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>添加账号</span>
      </div>
      <div class="text item">
        <section class="update-form">
          <el-form
            :model="ruleForm"
            status-icon
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="原密码" prop="oldpass">
              <el-input v-model="ruleForm.oldpass" show-password></el-input>
            </el-form-item>
            <el-form-item label="新密码" prop="pass">
              <el-input v-model="ruleForm.pass" show-password autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="checkPass">
              <el-input v-model="ruleForm.checkPass" show-password autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item class="upbtn">
              <el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
              <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
          </el-form>
        </section>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error("请输入6位数以上的密码"));
      } else {
        if (this.ruleForm.checkPass !== value) {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error("请输入6位数以上的密码"));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        oldpass:"",
        pass: "",
        checkPass: ""
      },

      rules: {
        oldpass: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, message: "长度在6个字符以上", trigger: "blur" }
        ],
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm() {
      this.$refs.ruleForm.validate(async result => {
        if (result) {
          if (window.confirm("你确定修改密码吗？")) {
            let userobj={
                oldPassword:this.ruleForm.oldpass,
                newPassword:this.ruleForm.pass,
                id:sessionStorage.getItem("id"),
                token:sessionStorage.getItem("token")
            }
            
           let data=await this.$axios.post("users.php?a=chgpwd",this.qs.stringify(userobj))
            // console.log(data)
                if (data.data.code == 0) {
                  this.$message({
                    message: data.data.msg,
                    type: "success"
                  });
                  this.del();
                  this.$router.replace("/");
                } else{
                  this.$message.error("请填写正确的信息");
                }
             
          }
        } else {
          
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    del() {
      sessionStorage.clear()
    }
  }
};
</script>

<style >
.update-pwd .update-form {
  margin: 0;
  width: 100% !important;
}

.update-form {
  width: 100%;
  background: #ffffff;
}
.update-form .el-form-item__content {
  width: 220px;
}
.update-form .el-form {
  width: 300px;
  margin-left: 57px;
}
.update-form {
  padding: 30px 0 20px;
}
.el-form-item--feedback .el-input__validateIcon {
  display: none;
}
</style>